<template>
	<div
		:class="{
			[$s.ImageSelectionContainer]: true,
			[$s.ImageSelectionContainerError]: hasError
		}"
		role="img"
		:style="bgImageStyle"
	>
		<div
			v-if="isUploading"
			:class="$s.ImageSelectionLoaderContainer"
		>
			<loading :class="$s.ImageSelectionLoader" />
		</div>

		<progress-bar
			:class="$s.ImageSelectionProgressContainer"
			:style="progressContainerStyle"
			:progress="image.progress"
			size="xsmall"
		/>

		<button
			v-if="!isUploading"
			type="button"
			:class="$s.ImageSelectionRemoveButton"
			@click="$emit('removeImage', image.id)"
		>
			<x-icon
				inline
				:class="$s.ImageSelectionRemoveIcon"
			/>
		</button>
	</div>
</template>

<script>
import { MLoading as Loading } from '@square/maker/components/Loading';
import { MProgressBar as ProgressBar } from '@square/maker/components/ProgressBar';
import XIcon from '@square/maker-icons/X';
import { IMAGE_SELECTOR_STATUSES } from './constants';

const MAX_PROGRESS = 100;
const PROGRESS_OPACITY = { shown: 1, hidden: 0 };

export default {
	name: 'MImageSelection',

	components: {
		Loading,
		ProgressBar,
		XIcon,
	},

	props: {
		image: {
			type: Object,
			required: true,
		},
	},

	computed: {
		bgImageStyle() {
			const url = `url("${this.image.url}")`;
			const loadingGradient = 'linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5))';
			const loadedGradient = 'linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 50%)';

			return {
				backgroundImage: this.showProgressBar
					? `${loadingGradient}, ${url}`
					: `${loadedGradient}, ${url}`,
				backgroundRepeat: 'no-repeat',
				backgroundPosition: 'center',
				backgroundSize: 'cover',
			};
		},

		showProgressBar() {
			return this.image.progress < MAX_PROGRESS;
		},

		progressContainerStyle() {
			return {
				opacity: this.showProgressBar ? PROGRESS_OPACITY.shown : PROGRESS_OPACITY.hidden,
			};
		},

		progressStyle() {
			return {
				width: `${this.image.progress}%`,
			};
		},

		isUploading() {
			return this.image.status === IMAGE_SELECTOR_STATUSES.PENDING;
		},

		hasError() {
			return this.image.status === IMAGE_SELECTOR_STATUSES.ERROR;
		},
	},
};
</script>

<style module="$s">
.ImageSelectionContainer {
	position: relative;
	box-sizing: border-box;
	width: 96px;
	height: 96px;
	overflow: hidden;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	border: 1px solid rgba(0, 0, 0, 0.15);
	border-radius: 8px;
	transition: background-image linear 150ms;

	/* these should later be pulled from
	the ThemeProvider component */
	--color-border: rgba(0, 0, 0, 0.3);
	--color-border-active: rgba(0, 0, 0, 0.9);
	--color-background: rgba(0, 0, 0, 0.9);
	--color-foreground: rgba(255, 255, 255, 0.95);
	--color-disabled: rgba(0, 0, 0, 0.05);
	--color-disabled-checked: rgba(0, 0, 0, 0.15);
	--color-error: #ff3b30;
}

.ImageSelectionLoaderContainer {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-position: center;
	background-size: cover;
}

.ImageSelectionContainerError {
	border: 2px solid var(--color-error);
}

.ImageSelectionRemoveButton {
	position: absolute;
	top: 8px;
	right: 8px;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 32px;
	height: 32px;
	background-color: #fff;
	border: 0;
	border-radius: 50%;
	cursor: pointer;
}

.ImageSelectionRemoveIcon {
	width: 16px;
	height: 16px;
}

.ImageSelectionProgressContainer {
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
}
</style>
